
body {
	background: #f6f5f7;
	display: flex;
	justify-content: center;
	font-family: 'Montserrat', sans-serif;
}

:root {
	margin-top: 5rem;
	
}

.SyApp {
	display: flex;
	flex-direction: column;
	background-color: white;
	border-radius: 5px;
	box-shadow: 8px 9px 7px 0px #ccc;
	padding: 2rem 0;
}

.SyApp .flexContainer {
	display: block;
	max-width: 280px;
	padding: 0 20px;

}

.SyApp .synonyms .wordTag{
	background-color: #FF4B2B;
	color: white;
	margin: 0.1rem 0.2rem;
	padding: 0.3rem;
	border-radius: 5px;
}

.SyApp button {
	border-radius: 1rem;
	border: 1px solid #FF4B2B;
	background-color: #FF4B2B;
	color: #FFFFFF;
	font-weight: bold;
	padding: 12px 45px;
}

.SyApp input {
	background-color: #eee;
	border: none;
	padding: 12px 15px;
}

.SyApp .submitButton:hover {
	background-color: #ccc;
	border: none;
	
}

.SyApp form {
	width: 100%;
	margin: 10px 0;
}